































































































































































.errMsg {
    color: red;
}

.loginBox {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: url("https://cdn.jsdelivr.net/npm/dldf-cdn@1.0.1/dist/bgsmall.png") no-repeat;
    background-size: cover;
    -webkit-background-size: cover;
    -o-background-size: cover;
    background-position: center center;
}

.login-title {
    color: #fff;
    letter-spacing: 10px;
    font-size: 48px;
    margin-bottom: 70px;
    margin-left: -8px;
}

.fromBox {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 126px;
    width: 360px;
    height: 500px;
    color: #fff;

    ::v-deep .el-input__inner {
        background: none;
        border: none;
        color: #fff;
        padding: 0 0px;
        border-bottom: 1px solid rgba(255, 255, 255, 0.28);
    }

    ::v-deep .el-input__inner ::placeholder {
        color: rgba(255, 255, 255, 0.4);
    }
}

.code-box {
    position: relative;
}

.code-img {
    position: absolute;
    cursor: pointer;
    right: 0;
    width: 108px;
    height: 44px;
    border-radius: 2px;
    border: 1px solid #4e555e;
    bottom: -1px;
}

.rember-box {
    ::v-deep.el-checkbox__label {
        color: rgba(255, 255, 255, 0.4);
    }
}

.sumbtn-box {
    margin-top: 50px;
    display: flex;
    justify-content: flex-end;

    div {
        width: 100px;
        height: 35px;
        text-align: center;
        line-height: 35px;
        border-radius: 4px;
        cursor: pointer;
    }

    // div:nth-child(1) {
    //   background: #fff;
    //   color: #333;
    //   margin-right: 30px;
    // }
    div:nth-child(1) {
        background: #2e4af7;
        color: #fff;

    }


}

.version-label {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    text-align: center;
    color: #fff;
    font-size: 12px;
}
